:root {
  /* Default theme - Soft, calming palette */
  --bg-primary-light: linear-gradient(135deg, #a8e6cf 0%, #dcedc1 50%, #ffd3a5 100%);
  --bg-secondary-light: rgba(255, 255, 255, 0.85);
  --text-primary-light: #2d5016;
  --text-secondary-light: #4a6741;
  --text-muted-light: #6b7c6b;
  --input-bg-light: rgba(255, 255, 255, 0.95);
  --input-text-light: #2d5016;
  --shadow-light: rgba(45, 80, 22, 0.15);
  --accent-light: #81c784;
  --accent-hover-light: #66bb6a;
  --warning-light: #ffb74d;
  --warning-hover-light: #ffa726;
  --danger-light: #e57373;
  --danger-hover-light: #ef5350;
  --info-light: #64b5f6;
  --info-hover-light: #42a5f5;
  
  --bg-primary-dark: linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f3460 100%);
  --bg-secondary-dark: rgba(26, 26, 46, 0.8);
  --text-primary-dark: #e8f5e8;
  --text-secondary-dark: #b8d4b8;
  --text-muted-dark: #8bb38b;
  --input-bg-dark: rgba(26, 26, 46, 0.6);
  --input-text-dark: #e8f5e8;
  --shadow-dark: rgba(0, 0, 0, 0.4);
  --accent-dark: #4fc3f7;
  --accent-hover-dark: #29b6f6;
  --warning-dark: #ffb74d;
  --warning-hover-dark: #ffa726;
  --danger-dark: #e57373;
  --danger-hover-dark: #ef5350;
  --info-dark: #64b5f6;
  --info-hover-dark: #42a5f5;
}

/* Dracula Theme */
[data-theme="dracula"] {
  --bg-primary-light: linear-gradient(135deg, #f8f8f2 0%, #f1f1eb 50%, #e8e8e0 100%);
  --bg-secondary-light: rgba(248, 248, 242, 0.9);
  --text-primary-light: #282a36;
  --text-secondary-light: #44475a;
  --text-muted-light: #6272a4;
  --input-bg-light: rgba(248, 248, 242, 0.95);
  --input-text-light: #282a36;
  --shadow-light: rgba(40, 42, 54, 0.15);
  --accent-light: #50fa7b;
  --accent-hover-light: #3dd865;
  --warning-light: #ffb86c;
  --warning-hover-light: #ffa94d;
  --danger-light: #ff5555;
  --danger-hover-light: #ff3333;
  --info-light: #8be9fd;
  --info-hover-light: #6dd5f7;
  
  --bg-primary-dark: linear-gradient(135deg, #282a36 0%, #21222c 50%, #1e1f29 100%);
  --bg-secondary-dark: rgba(40, 42, 54, 0.9);
  --text-primary-dark: #f8f8f2;
  --text-secondary-dark: #e6e6e0;
  --text-muted-dark: #6272a4;
  --input-bg-dark: rgba(40, 42, 54, 0.8);
  --input-text-dark: #f8f8f2;
  --shadow-dark: rgba(0, 0, 0, 0.5);
  --accent-dark: #50fa7b;
  --accent-hover-dark: #3dd865;
  --warning-dark: #ffb86c;
  --warning-hover-dark: #ffa94d;
  --danger-dark: #ff5555;
  --danger-hover-dark: #ff3333;
  --info-dark: #8be9fd;
  --info-hover-dark: #6dd5f7;
}

/* Solarized Theme */
[data-theme="solarized"] {
  --bg-primary-light: linear-gradient(135deg, #fdf6e3 0%, #eee8d5 50%, #e6e0c8 100%);
  --bg-secondary-light: rgba(253, 246, 227, 0.9);
  --text-primary-light: #073642;
  --text-secondary-light: #586e75;
  --text-muted-light: #93a1a1;
  --input-bg-light: rgba(253, 246, 227, 0.95);
  --input-text-light: #073642;
  --shadow-light: rgba(7, 54, 66, 0.15);
  --accent-light: #859900;
  --accent-hover-light: #6b7c00;
  --warning-light: #b58900;
  --warning-hover-light: #9a7500;
  --danger-light: #dc322f;
  --danger-hover-light: #c62d2a;
  --info-light: #268bd2;
  --info-hover-light: #1e7bb8;
  
  --bg-primary-dark: linear-gradient(135deg, #002b36 0%, #001f2a 50%, #001419 100%);
  --bg-secondary-dark: rgba(0, 43, 54, 0.9);
  --text-primary-dark: #fdf6e3;
  --text-secondary-dark: #eee8d5;
  --text-muted-dark: #93a1a1;
  --input-bg-dark: rgba(0, 43, 54, 0.8);
  --input-text-dark: #fdf6e3;
  --shadow-dark: rgba(0, 0, 0, 0.6);
  --accent-dark: #859900;
  --accent-hover-dark: #6b7c00;
  --warning-dark: #b58900;
  --warning-hover-dark: #9a7500;
  --danger-dark: #dc322f;
  --danger-hover-dark: #c62d2a;
  --info-dark: #268bd2;
  --info-hover-dark: #1e7bb8;
}

/* Nord Theme */
[data-theme="nord"] {
  --bg-primary-light: linear-gradient(135deg, #eceff4 0%, #e5e9f0 50%, #d8dee9 100%);
  --bg-secondary-light: rgba(236, 239, 244, 0.9);
  --text-primary-light: #2e3440;
  --text-secondary-light: #3b4252;
  --text-muted-light: #4c566a;
  --input-bg-light: rgba(236, 239, 244, 0.95);
  --input-text-light: #2e3440;
  --shadow-light: rgba(46, 52, 64, 0.15);
  --accent-light: #88c0d0;
  --accent-hover-light: #5e81ac;
  --warning-light: #ebcb8b;
  --warning-hover-light: #d08770;
  --danger-light: #bf616a;
  --danger-hover-light: #a54e56;
  --info-light: #81a1c1;
  --info-hover-light: #5e81ac;
  
  --bg-primary-dark: linear-gradient(135deg, #2e3440 0%, #3b4252 50%, #434c5e 100%);
  --bg-secondary-dark: rgba(46, 52, 64, 0.9);
  --text-primary-dark: #eceff4;
  --text-secondary-dark: #e5e9f0;
  --text-muted-dark: #4c566a;
  --input-bg-dark: rgba(46, 52, 64, 0.8);
  --input-text-dark: #eceff4;
  --shadow-dark: rgba(0, 0, 0, 0.5);
  --accent-dark: #88c0d0;
  --accent-hover-dark: #5e81ac;
  --warning-dark: #ebcb8b;
  --warning-hover-dark: #d08770;
  --danger-dark: #bf616a;
  --danger-hover-dark: #a54e56;
  --info-dark: #81a1c1;
  --info-hover-dark: #5e81ac;
}

/* Tokyo Night Theme */
[data-theme="tokyo-night"] {
  --bg-primary-light: linear-gradient(135deg, #d5d6db 0%, #c0c0c0 50%, #a9b1d6 100%);
  --bg-secondary-light: rgba(213, 214, 219, 0.9);
  --text-primary-light: #1a1b26;
  --text-secondary-light: #2d3748;
  --text-muted-light: #565f89;
  --input-bg-light: rgba(213, 214, 219, 0.95);
  --input-text-light: #1a1b26;
  --shadow-light: rgba(26, 27, 38, 0.15);
  --accent-light: #7aa2f7;
  --accent-hover-light: #5a8ae8;
  --warning-light: #e0af68;
  --warning-hover-light: #d4a657;
  --danger-light: #f7768e;
  --danger-hover-light: #f55a7a;
  --info-light: #7dcfff;
  --info-hover-light: #5bb8e8;
  
  --bg-primary-dark: linear-gradient(135deg, #1a1b26 0%, #16161e 50%, #0f0f14 100%);
  --bg-secondary-dark: rgba(26, 27, 38, 0.9);
  --text-primary-dark: #a9b1d6;
  --text-secondary-dark: #9aa5ce;
  --text-muted-dark: #565f89;
  --input-bg-dark: rgba(26, 27, 38, 0.8);
  --input-text-dark: #a9b1d6;
  --shadow-dark: rgba(0, 0, 0, 0.6);
  --accent-dark: #7aa2f7;
  --accent-hover-dark: #5a8ae8;
  --warning-dark: #e0af68;
  --warning-hover-dark: #d4a657;
  --danger-dark: #f7768e;
  --danger-hover-dark: #f55a7a;
  --info-dark: #7dcfff;
  --info-hover-dark: #5bb8e8;
}

.App {
  text-align: center;
  min-height: 100vh;
  color: var(--text-primary-light);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  transition: all 0.3s ease;
}

.App.light-mode {
  background: var(--bg-primary-light);
}

.App.dark-mode {
  background: var(--bg-primary-dark);
}

.App-header {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  min-height: 100vh;
}

h1 {
  font-size: 3rem;
  margin-bottom: 2rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.timer-setup {
  background: var(--bg-secondary-light);
  padding: 2rem;
  border-radius: 15px;
  backdrop-filter: blur(10px);
  margin-bottom: 2rem;
  box-shadow: 0 8px 32px var(--shadow-light);
  transition: all 0.3s ease;
}

.duration-input {
  margin-bottom: 1.5rem;
}

.duration-input label {
  display: block;
  margin-bottom: 0.5rem;
  font-size: 1.1rem;
  font-weight: 500;
}

.duration-input input {
  padding: 0.75rem;
  font-size: 1.2rem;
  border: none;
  border-radius: 8px;
  width: 120px;
  text-align: center;
  background: var(--input-bg-light);
  color: var(--input-text-light);
  transition: all 0.3s ease;
}

.dark-mode .duration-input input {
  background: var(--input-bg-dark);
  color: var(--input-text-dark);
}

.start-btn {
  background: var(--accent-light);
  color: white;
  border: none;
  padding: 1rem 2rem;
  font-size: 1.2rem;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px var(--shadow-light);
}

.App.dark-mode .start-btn {
  background: var(--accent-dark);
  box-shadow: 0 4px 15px var(--shadow-dark);
}

.start-btn:hover {
  background: var(--accent-hover-light);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px var(--shadow-light);
}

.App.dark-mode .start-btn:hover {
  background: var(--accent-hover-dark);
  box-shadow: 0 6px 20px var(--shadow-dark);
}

.timer-running {
  background: var(--bg-secondary-light);
  padding: 3rem;
  border-radius: 15px;
  backdrop-filter: blur(10px);
  margin-bottom: 2rem;
  box-shadow: 0 8px 32px var(--shadow-light);
  transition: all 0.3s ease;
}

.time-display {
  font-size: 4rem;
  font-weight: bold;
  margin-bottom: 2rem;
  font-family: 'Courier New', monospace;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.lost-focus-btn {
  background: var(--danger-light);
  color: white;
  border: none;
  padding: 1rem 2rem;
  font-size: 1.2rem;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px var(--shadow-light);
}

.App.dark-mode .lost-focus-btn {
  background: var(--danger-dark);
  box-shadow: 0 4px 15px var(--shadow-dark);
}

.lost-focus-btn:hover {
  background: var(--danger-hover-light);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px var(--shadow-light);
}

.App.dark-mode .lost-focus-btn:hover {
  background: var(--danger-hover-dark);
  box-shadow: 0 6px 20px var(--shadow-dark);
}

.name-prompt {
  background: var(--bg-secondary-light);
  padding: 2rem;
  border-radius: 15px;
  backdrop-filter: blur(10px);
  margin-bottom: 2rem;
  box-shadow: 0 8px 32px var(--shadow-light);
  transition: all 0.3s ease;
}

.name-prompt h3 {
  margin-bottom: 1rem;
