}

.cancel-clear-btn {
  background: #9E9E9E;
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.cancel-clear-btn:hover {
  background: #757575;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
}

/* Responsive design */
@media (max-width: 768px) {
  .App-header {
    padding: 1rem;
  }
  
  h1 {
    font-size: 2rem;
  }
  
  .time-display {
    font-size: 3rem;
  }
  
  .name-prompt input {
    width: 250px;
  }
  
  .break-buttons {
    flex-direction: column;
    align-items: center;
  }
  
  .session-item {
    flex-direction: column;
    text-align: center;
    gap: 0.5rem;
  }
  
  .session-duration {
    margin: 0;
    min-width: auto;
  }
  
  .clear-confirm-buttons {
    flex-direction: column;
    align-items: center;
  }
  
  .clear-confirm-modal {
    margin: 1rem;
    padding: 1.5rem;
  }
}

/* Info Icon */
.info-icon {
  position: absolute;
  top: 2rem;
  right: 2rem;
  background: var(--bg-secondary-light);
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  font-size: 18px;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 15px var(--shadow-light);
  display: flex;
  align-items: center;
  justify-content: center;
}

.App.dark-mode .info-icon {
  background: var(--bg-secondary-dark);
  box-shadow: 0 4px 15px var(--shadow-dark);
}

.info-icon:hover {
  transform: scale(1.1);
  background: rgba(255, 255, 255, 0.2);
}

.App.dark-mode .info-icon:hover {
  background: rgba(255, 255, 255, 0.1);
}

/* Modal Overlay */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(5px);
}

/* Welcome Modal */
.welcome-modal {
  background: var(--bg-secondary-light);
  border-radius: 20px;
  padding: 2rem;
  max-width: 600px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  backdrop-filter: blur(20px);
  box-shadow: 0 20px 40px var(--shadow-light);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.App.dark-mode .welcome-modal {
  background: var(--bg-secondary-dark);
  box-shadow: 0 20px 40px var(--shadow-dark);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.welcome-modal h2 {
  color: var(--text-primary-light);
  margin-bottom: 1.5rem;
  font-size: 2rem;
}

.App.dark-mode .welcome-modal h2 {
  color: var(--text-primary-dark);
}

.welcome-modal h3 {
  color: var(--text-primary-light);
  margin: 1.5rem 0 0.5rem 0;
  font-size: 1.2rem;
}

.App.dark-mode .welcome-modal h3 {
  color: var(--text-primary-dark);
}

.welcome-modal p {
  color: var(--text-secondary-light);
  line-height: 1.6;
  margin-bottom: 1rem;
}

.App.dark-mode .welcome-modal p {
  color: var(--text-secondary-dark);
}

.welcome-modal ol, .welcome-modal ul {
  color: var(--text-secondary-light);
  text-align: left;
  margin: 1rem 0;
  padding-left: 1.5rem;
}

.App.dark-mode .welcome-modal ol, .App.dark-mode .welcome-modal ul {
  color: var(--text-secondary-dark);
}

.welcome-modal li {
  margin-bottom: 0.5rem;
  line-height: 1.5;
}

.welcome-modal .tip {
  background: rgba(255, 255, 255, 0.1);
  padding: 1rem;
  border-radius: 10px;
  border-left: 4px solid #4CAF50;
  margin-top: 1.5rem;
  font-style: italic;
}

.App.dark-mode .welcome-modal .tip {
  background: rgba(255, 255, 255, 0.05);
}

/* Info Modal */
.info-modal {
  background: var(--bg-secondary-light);
  border-radius: 20px;
  padding: 2rem;
  max-width: 500px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  backdrop-filter: blur(20px);
  box-shadow: 0 20px 40px var(--shadow-light);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.App.dark-mode .info-modal {
  background: var(--bg-secondary-dark);
  box-shadow: 0 20px 40px var(--shadow-dark);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.info-modal h2 {
  color: var(--text-primary-light);
  margin-bottom: 1.5rem;
  font-size: 1.8rem;
}

.App.dark-mode .info-modal h2 {
  color: var(--text-primary-dark);
}

.info-modal h3 {
  color: var(--text-primary-light);
  margin: 1.5rem 0 0.5rem 0;
  font-size: 1.1rem;
}

.App.dark-mode .info-modal h3 {
  color: var(--text-primary-dark);
}

.info-modal p {
  color: var(--text-secondary-light);
  line-height: 1.6;
  margin-bottom: 1rem;
}

.App.dark-mode .info-modal p {
  color: var(--text-secondary-dark);
}

.info-modal ol, .info-modal ul {
  color: var(--text-secondary-light);
  text-align: left;
  margin: 1rem 0;
  padding-left: 1.5rem;
}

.App.dark-mode .info-modal ol, .App.dark-mode .info-modal ul {
  color: var(--text-secondary-dark);
}

.info-modal li {
  margin-bottom: 0.5rem;
  line-height: 1.5;
}

.info-modal .tip {
  background: rgba(255, 255, 255, 0.1);
  padding: 1rem;
  border-radius: 10px;
  border-left: 4px solid #4CAF50;
  margin-top: 1.5rem;
  font-style: italic;
}

.App.dark-mode .info-modal .tip {
  background: rgba(255, 255, 255, 0.05);
}

/* Responsive adjustments for modals */
@media (max-width: 768px) {
  .info-icon {
    top: 1rem;
    right: 1rem;
    width: 35px;
    height: 35px;
    font-size: 16px;
  }
  
  .welcome-modal, .info-modal {
    margin: 1rem;
    padding: 1.5rem;
    max-height: 85vh;
  }
  
  .welcome-modal h2 {
    font-size: 1.5rem;
  }
  
  .info-modal h2 {
    font-size: 1.4rem;
  }
}

/* Theme Selector */
.theme-selector {
  position: absolute;
  top: 2rem;
  left: 2rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  z-index: 100;
}

.theme-button {
  width: 40px;
  height: 40px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px var(--shadow-light);
  position: relative;
  overflow: hidden;
}

.App.dark-mode .theme-button {
  box-shadow: 0 4px 15px var(--shadow-dark);
}

.theme-button:hover {
  transform: scale(1.1);
  box-shadow: 0 6px 20px var(--shadow-light);
}

.App.dark-mode .theme-button:hover {
  box-shadow: 0 6px 20px var(--shadow-dark);
}

.theme-button.active {
  transform: scale(1.15);
  box-shadow: 0 0 0 3px var(--accent-light);
}

.App.dark-mode .theme-button.active {
  box-shadow: 0 0 0 3px var(--accent-dark);
}

/* Theme color previews */
.theme-default {
  background: linear-gradient(135deg, #a8e6cf 0%, #dcedc1 50%, #ffd3a5 100%);
}

.theme-dracula {
  background: linear-gradient(135deg, #f8f8f2 0%, #282a36 50%, #ff5555 100%);
}

.theme-solarized {
  background: linear-gradient(135deg, #fdf6e3 0%, #eee8d5 50%, #859900 100%);
}

.theme-nord {
  background: linear-gradient(135deg, #eceff4 0%, #e5e9f0 50%, #88c0d0 100%);
}

.theme-tokyo-night {
  background: linear-gradient(135deg, #d5d6db 0%, #c0c0c0 50%, #7aa2f7 100%);
}

/* Theme selector tooltip */
.theme-button::after {
  content: attr(data-theme-name);
  position: absolute;
  left: 50px;
  top: 50%;
  transform: translateY(-50%);
  background: var(--bg-secondary-light);
  color: var(--text-primary-light);
  padding: 0.5rem 1rem;
  border-radius: 8px;
  font-size: 0.9rem;
  white-space: nowrap;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;
  box-shadow: 0 4px 15px var(--shadow-light);
  z-index: 1000;
}

.App.dark-mode .theme-button::after {
  background: var(--bg-secondary-dark);
  color: var(--text-primary-dark);
  box-shadow: 0 4px 15px var(--shadow-dark);
}

.theme-button:hover::after {
  opacity: 1;
}

/* Responsive adjustments for theme selector */
@media (max-width: 768px) {
  .theme-selector {
    top: 1rem;
    left: 1rem;
    gap: 0.3rem;
  }
  
  .theme-button {
    width: 35px;
    height: 35px;
  }
  
  .theme-button::after {
    left: 40px;
    font-size: 0.8rem;
    padding: 0.4rem 0.8rem;
  }
}
